import axios from 'axios'
import {getToken} from './Utils'

//http://192.168.1.135:80
//https://sportsment.herokuapp.com/
let endPoint = 'https://sportsment.herokuapp.com'

export async function Login(username, password) {
    let res = await axios.post(`${endPoint}/auth/login`, { username: username.toLowerCase(), password: password })
    return res.data
}

export async function GetUserInfo() {
    let res = await axios.post(`${endPoint}/auth/userinfo`, {token: getToken()})
    return res.data
}

export async function Register(username, password, following){
    let res = await axios.post(`${endPoint}/auth/register`, { username: username.toLowerCase(), password: password, following: following })
    return res.data
}

export async function SubmitPost(title, body, tags, time){
    let res = await axios.post(`${endPoint}/posts/create`, {title: title, body: body, tags: tags, time: time, token: getToken()})
    return res.data
}

export async function GetPosts(tags){
    let res = await axios.post(`${endPoint}/posts/getposts`, {tags: tags})
    return res.data
}

export async function DoVote(voteType, tag, postId){
    let res = await axios.post(`${endPoint}/posts/vote`, {token: getToken(), voteType: voteType, tag: tag, postId: postId})
    return res.data
}

export async function GetTeams(){
    let res = await axios.get(`${endPoint}/teams/getteams`)
    return res.data
}

export async function GetLeagues(){
    let res = await axios.get(`${endPoint}/leagues/getleagues`)
    return res.data
}

export async function GetFixturesInLeague(leagueId){
    let res = await axios.post(`${endPoint}/fixtures/getfixtures`, {leagueId: leagueId, token: getToken()})
    return res.data
}

export async function GetFixtureDetails(fixtureId){
    let res = await axios.post(`${endPoint}/fixtures/getfixturedetails`, {fixtureId: fixtureId, token: getToken()})
    return res.data
}

export async function GetTeamMatches(team){
    let res = await axios.post(`${endPoint}/teams/getteammatches`, {team: team, token: getToken()})
    return res.data
}