import React, { createContext, Component, } from 'react'

export const User = createContext(null)
export const Page = createContext(null)

export class PageProvider extends Component {

    constructor(props) {
        super(props)

        this.setPage = (tag) => {
            this.setState(state => {

                for (let item of tag) {
                    if (!state.tags.includes(item)) {
                        state.tags.push(item)
                    }
                }

                state.currentTags = tag
                return tag
            })
        }

        this.popPage = (tag) => {
            this.setState(state => {
                if (this.state.tags.includes(tag)) {
                    if (state.tags.length == 1)
                        return

                    state.tags.pop()
                    state.currentTags = state.tags[state.tags.length - 1]
                    
                    return state.currentTags
                }
            })
        }

        this.state = {
            tags: ['home'],
            currentTags: ['home'],
            setPage: this.setPage,
            popPage: this.popPage
        }
    }

    render() {
        return (
            <Page.Provider value={this.state}>
                {this.props.children}
            </Page.Provider>
        )
    }
}

class UserProvider extends Component {
    constructor(props) {
        super(props)

        this.editUser = (details) => {
            this.setState(state => {
                state.username = details.username
                state.followingSports = details.followingSports == undefined ? [] : details.followingSport
                state.followingTeams = details.followingTeams == undefined ? [] : details.followingTeams
                state.followingCompetitions = details.followingCompetitions == undefined ? [] : details.followingCompetitions
                return details
            })
        }

        this.state = {
            editUser: this.editUser,
            followingSports: [],
            followingCompetitions: [],
            followingTeams: [],
            username: null,
            token: null
        }
    }

    render() {
        return (
            <User.Provider value={this.state}>
                {this.props.children}
            </User.Provider>
        )
    }
}

export default UserProvider