import React, { useState, useContext, useEffect } from 'react'
import { setLoginModalVisibility, setRegisterModalVisibility } from './Modal'
import { User } from '../Config'
import { Dropdown } from './Dropdown'
import { RegisterPage, enablePage } from './RegisterPage'

function LeftMenu(props) {

  if (!props.activated)
    return null

  return (
    <div id="leftmenu">
      <CredentialBar></CredentialBar>
      <RegisterPage></RegisterPage>
      <MenuColumn></MenuColumn>
      <div id="menu-divider">
        <div className="line"></div>
        <p className="divider-text">FOLLOWING</p>
        <div className="line"></div>
      </div>
      <FollowingContainer></FollowingContainer>
    </div>
  )
}

function CredentialBar() { //Returns Register/login or profile/notification

  return (
    <User.Consumer>
      {user => {
        if (user.username == null) {
          return (<div id="credential-bar">
            <div className="button fixed" onClick={() => { enablePage() }}>
              <div className="items">
                <div className="leading">
                  <div className="icon dot"></div>
                  <p className="text">REGISTER</p>
                </div>
              </div>
              <div className="shadow"></div>
            </div>
            <div className="button fixed" onClick={() => { setLoginModalVisibility(true) }}>
              <div className="items">
                <div className="leading">
                  <div className="icon dot"></div>
                  <p className="text">LOGIN</p>
                </div>
              </div>
              <div className="shadow"></div>
            </div>
          </div>)
        } else {
          return (
            <div id="credential-bar">
              <Dropdown title={user.username}></Dropdown>
              <i className="notification fas fa-bell"></i>
            </div>)
        }
      }}
    </User.Consumer>
  )
}

function MenuColumn() {
  return (
    <div id="menu-column">
      <MenuButton onPress={() => { }} title='HOME' iconClass='fas fa-home'></MenuButton>
      <MenuButton onPress={() => { }} title='MODERATION' iconClass='fas fa-user-friends'></MenuButton>
      <MenuButton onPress={() => { }} title='HELP' iconClass='fas fa-question-circle'></MenuButton>
      <MenuButton onPress={() => { }} title='FEEDBACK' iconClass='fas fa-comments'></MenuButton>
    </div>
  )
}

function FollowingContainer() {
  return (
    <div id="following-container">
      <User.Consumer>
        {
          user => {
            return (<>
              <FollowingButton title={"Sports"} items={user.followingSports}></FollowingButton>
              <FollowingButton title={"Competitions"} items={user.followingCompetitions}></FollowingButton>
              <FollowingButton title={"Teams"} items={user.followingTeams}></FollowingButton>
            </>)
          }
        }
      </User.Consumer>
    </div>
  )
}

function MenuButton(props) {
  return (
    <div className="button gap" onClick={() => { props.onPress() }}>
      <div className="items">
        <div className="leading">
          <i className={`icon ${props.iconClass}`}></i>
          <p className="text">{props.title}</p>
        </div>
        <div className="dot"></div>
      </div>
      <div className="shadow"></div>
    </div>
  )
}

function FollowingButton(props) {

  const [style, setStyle] = useState('')

  return (
    <div className="button gap" onClick={() => { style == '' ? setStyle('open') : setStyle('') }}>
      <div className="items">
        <div className="leading">
          <div className="icon dot"></div>
          <p className="text">{props.title}</p>
        </div>
        <i className="trailing fas fa-plus-circle"></i>
      </div>
      <div className={`follow-body ${style}`}>
        {
          props.items.map(item => <p className="follow-item" key={item}>{item}</p>)
        }
      </div>
      <div className="shadow"></div>
    </div>
  )
}

