import React, { useEffect, useState } from 'react'
import { GetTeams, GetLeagues, GetFixturesInLeague, GetTeamMatches } from '../Communicator'
import { showNotification } from './NoticationBar'
import { ShowContentPage } from './ContentPage'
import ContentPage from '../components/ContentPage'
import { Page } from '../Config'
import { setLoadScreen } from './LoadDialog'
import { useTraceUpdate } from '../Utils'

let updateFixtures
let updateTab

let backButtonListenerStack = []

function RightMenu(props) {

    const [teams, setTeams] = useState([])
    const [leagues, setLeagues] = useState([])
    const [tab, setTab] = useState('home')
    const [fixtures, setFixtures] = useState([])
    const [isContentPageVisible, setContentPageVisibility] = useState(false)

    useEffect(() => {
        async function getData() {
            let res_team = await GetTeams()
            let res_league = await GetLeagues()

            if (res_team.status === "failure" || res_league.status === "failure")
                return console.log("Error occured while retrieving leagues/teams")

            setTeams(res_team.data.teams)
            setLeagues(res_league.data.leagues)
        }

        updateFixtures = setFixtures
        updateTab = setTab
        getData()
    }, [])

    if (!props.activated)
        return null

    return (
        <Page.Consumer>
            {page =>
                <div id="rightmenu">
                    {tab === 'home' ? <div></div> : <div id="back-btn" onClick={() => { backButtonListenerStack.pop()() }} className="button fixed">
                        <p> BACK </p>
                    </div>}
                    <div id="navigation-container">
                        <Tabs setContent={setContentPageVisibility} tab={tab} setTab={setTab} page={page}></Tabs>
                        <div id="navigation-body">
                            <ContentPage isActivated={isContentPageVisible}></ContentPage>
                            <NavigationBody setContent={setContentPageVisibility} page={page} fixtures={fixtures} tab={tab} teams={teams} leagues={leagues}></NavigationBody>
                        </div>
                    </div>
                </div>
            }
        </Page.Consumer>
    )
}

function tabClick(tab, setTab, page, name) {
    if (tab === name)
        return

    let lastTab = page.tags[page.tags.length - 1]
    page.popPage(lastTab)
    setTab(name)
    page.setPage([name])


    // backButtonListenerStack.push(() => {
    //     updateTab(page.tags.length > 1 ? page.tags[page.tags.length - 2] : 'home')
    //     page.popPage(name)
    // })
}

function Tabs(props) {
    let tab = props.tab
    let setTab = props.setTab
    let page = props.page
    let setContent = props.setContent

    if (tab === 'home') {
        return (<></>)
    }
    else if (tab === 'competition' || tab === 'team' || tab === 'fixture') {
        return (
            <div id="navigation-tab">
                <div onClick={() => { tabClick(tab, setTab, page, 'competition') }} className="tab"><p className={tab === 'competition' ? 'dark' : 'light'}>COMPETITIONS</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, 'team') }} className="tab"><p className={tab === 'team' ? 'dark' : 'light'}>TEAMS</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, 'fixture') }} className="tab"><p className={tab === 'fixture' ? 'dark' : 'light'}>IN-PLAY</p></div>
            </div>
        )
    }
    else if (tab.includes('_competition_result') || tab.includes('_competition_fixture') || tab.includes('_competition_inplay') || tab.includes('_competition_team')) {
        let currentPage = tab.split('_')[0]

        return (
            <div id="navigation-tab">
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_competition_result`); setContent(false) }} className="tab"><p className={tab.includes('_competition_result') ? 'dark' : 'light'}>RESULTS</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_competition_fixture`); setContent(false) }} className="tab"><p className={tab.includes('_competition_fixture') ? 'dark' : 'light'}>FIXTURES</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_competition_inplay`); setContent(false) }} className="tab"><p className={tab.includes('_competition_inplay') ? 'dark' : 'light'}>IN-PLAY</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_competition_team`); setContent(false) }} className="tab"><p className={tab.includes('_competition_team') ? 'dark' : 'light'}>TEAMS</p></div>
            </div>
        )
    }
    else if (tab.includes('_team_result') || tab.includes('_team_fixture') || tab.includes('_team_inplay')) {
        let currentPage = tab.split('_')[0]

        return (
            <div id="navigation-tab">
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_team_result`); setContent(false) }} className="tab"><p className={tab.includes('_team_result') ? 'dark' : 'light'}>RESULTS</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_team_fixture`); setContent(false) }} className="tab"><p className={tab.includes('_team_fixture') ? 'dark' : 'light'}>FIXTURES</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_team_inplay`); setContent(false) }} className="tab"><p className={tab.includes('_team_inplay') ? 'dark' : 'light'}>IN-PLAY</p></div>
            </div>
        )
    }
    else {
        return null
    }
}

function NavigationBody(props) {

    let setContent = props.setContent
    let page = props.page
    let tab = props.tab
    let leagues = props.leagues
    let teams = props.teams
    let fixtures = props.fixtures

    const [teamFixtures, setTeamFixtures] = useState([])

    console.log(page.tags)

    function competitionFixtureResolver(statusArray) {
        let click = async (fixture) => {
            backButtonListenerStack.push(() => {
                page.popPage(`${fixture.fixture_id}_fixture_details`)
                setContent(false)
                updateTab(tab)
            })

            setContent(true)
            page.setPage([`${fixture.fixture_id}_fixture_details`]) //fixture.homeTeam.team_name, fixture.awayTeam.team_name
            updateTab(`${fixture.fixture_id}_fixture_details`)
            ShowContentPage(true, fixture)
        }

        return (fixtures.filter((fixture) => statusArray.includes(fixture.statusShort)).map(fixture => (
            <Tile clicked={() => { click(fixture) }} key={fixture.fixture_id} name={`${fixture.homeTeam.team_name} VS ${fixture.awayTeam.team_name}`} id={fixture.fixture_id}></Tile>
        ))
        )
    }

    function teamFixtureResolver(statusArray) {
        let click = async (fixture) => {
            backButtonListenerStack.push(() => {
                page.popPage(`${fixture.fixture_id}_fixture_details`)
                setContent(false)
                updateTab(tab)
            })

            setContent(true)
            page.setPage([`${fixture.fixture_id}_fixture_details`]) //fixture.homeTeam.team_name, fixture.awayTeam.team_name
            updateTab(`${fixture.fixture_id}_fixture_details`)
            ShowContentPage(true, fixture)
        }

        return (teamFixtures.filter((fixture) => statusArray.includes(fixture.statusShort)).map(fixture => (
            <Tile clicked={() => { click(fixture) }} key={fixture.fixture_id} name={`${fixture.homeTeam.team_name} VS ${fixture.awayTeam.team_name}`} id={fixture.fixture_id}></Tile>
        ))
        )
    }

    if (tab === 'home') { //Sports Selection

        function click() {
            let lastTab = page.tags[page.tags.length - 1]

            updateTab('competition')
            page.setPage(['competition'])

            backButtonListenerStack.push(() => {
                page.popPage(lastTab)
                updateTab('home')
            })
        }

        return (
            <Tile name="Soccer" clicked={() => { click() }}></Tile>
        )
    }
    else if (tab === 'competition') { //Competition

        let click = async (league) => {
            setLoadScreen(true)
            let result = await GetFixturesInLeague(league.leagueId)
            setLoadScreen(false)

            if (result.status === "failure") {
                showNotification(result.message)
                return
            }

            updateFixtures(result.data)
            updateTab(`${league.name}_competition_result`)

            page.setPage([`${league.name}_competition_result`])

            backButtonListenerStack.push(() => {
                let lastTab = page.tags[page.tags.length - 1]

                page.popPage(lastTab)
                updateTab('competition')
            })
        }

        return (
            leagues.map(league => (
                <Tile key={league._id} name={league.name} clicked={() => { click(league) }}></Tile>
            ))
        )
    }
    else if (tab === 'team') { //Teams

        async function click(team) {
            setLoadScreen(true)
            let result = await GetTeamMatches(team.name)
            setTeamFixtures(result.data)
            setLoadScreen(false)

            page.setPage([`${team.name}_team_result`])
            updateTab(`${team.name}_team_result`)

            backButtonListenerStack.push(() => {
                let lastTab = page.tags[page.tags.length - 1]

                page.popPage(lastTab)
                updateTab('team')
            })
        }

        return (
            teams.map(team => (
                <Tile clicked={() => { click(team) }} key={team._id} name={team.name}> id={team.teamId}</Tile>
            ))
        )
    }
    else if (tab.includes('_competition_result')) { //In-League Matches
        return competitionFixtureResolver(["FT", "AET", "PEN"])
    }
    else if (tab.includes('_competition_fixture')) {
        return competitionFixtureResolver(["NS"])
    }
    else if (tab.includes('_competition_inplay')) {
        return competitionFixtureResolver(["1H", "HT", "2H", "ET", "P", "BT"])
    }
    else if (tab.includes('_competition_team')) {
        let selectedCompetition = tab.split('_')[0]

        function click(team) {

            page.setPage([`${team.name}_team_result`])
            updateTab(`${team.name}_team_result`)

            backButtonListenerStack.push(() => {
                let lastTab = page.tags[page.tags.length - 1]

                page.popPage(lastTab)
                updateTab('team')
            })
        }

        return teams.filter(team => team.leagueName == selectedCompetition).map(team => (
            <Tile clicked={() => { click(team) }} key={team._id} name={team.name}> id={team.teamId}</Tile>
        ))

        // return (
        //     teams.map(team => (
        //         <Tile clicked={() => { click(team) }} key={team._id} name={team.name}> id={team.teamId}</Tile>
        //     ))
        // )
    }
    else if (tab.includes('_team_result')) {
        return teamFixtureResolver(["FT", "AET", "PEN"])
    }
    else if(tab.includes('_team_fixture')){
        return teamFixtureResolver(["NS"])
    }
    else if(tab.includes('_team_inplay')){
        return teamFixtureResolver(["1H", "HT", "2H", "ET", "P", "BT"])
    }
    else {
        return null
    }
}

function Tile(props) {
    return (
        <div className="team-tile" onClick={props.clicked}>
            <i className="logo fas fa-igloo"></i>
            <div className="divider"></div>
            <p className="name">{props.name}</p>
        </div>
    )
}

export default RightMenu