import React, { useState } from 'react'
import { Login, Register, SubmitPost } from '../Communicator'
import { showNotification } from './NoticationBar'
import { User, Page } from '../Config'

let changeModalVisibility = null
let changeLoginModalVisibility = null
let changeRegisterModalVisibility = null
let changeCreatePostModalVisibility = null

export function AcceptModal() {
    const [visible, setVisible] = useState(false)
    changeModalVisibility = setVisible

    if (!visible)
        return null

    return (
        <div id="modal" onClick={() => { setAcceptModalVisibility(false) }}>
            <div id="dialog" onClick={(e) => { e.stopPropagation() }}>
                <p className="title">Are you sure?</p>
                <p className="description">The action you are about to take can have some serious consequences</p>
                <div className="space"></div>
                <div className="modal-row">
                    <div className="btn" onClick={() => { }}>OK</div>
                    <div className="btn" onClick={() => { }}>CANCEL</div>
                </div>
            </div>
        </div>
    )
}

export function LoginModal() {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [user, setUser] = useState(null)
    const [visible, setVisibility] = useState(false)
    changeLoginModalVisibility = setVisibility

    if (!visible)
        return null

    return (
        <div onClick={() => { setLoginModalVisibility(false) }} id="modal">
            <div className="login" id="dialog" onClick={(e) => { e.stopPropagation() }}>
                <p className="title">LOG-IN</p>
                <p>Username:</p>
                <input className="username" onChange={(event) => { setUsername(event.target.value) }} type="text"></input>
                <p>Password:</p>
                <input className="password" onChange={(event) => { setPassword(event.target.value) }} type="password"></input>
                <div className="space"></div>
                <User.Consumer>{
                    user => <div className="btn default" onClick={async () => {

                        let data = await Login(username, password)

                        if (data.status === "success") {
                            setLoginModalVisibility(false)
                            let now = new Date()
                            let time = now.getTime()
                            time += 3600000
                            now.setTime(time)
                            document.cookie = `token=${data.data.token}; expires=${now.toUTCString()};`
                            user.editUser({
                                username: username,
                                token: data.data.token,
                                followingSports: data.data.followedSports,
                                followingCompetitions: data.data.followedCompetitions,
                                followingTeams: data.data.followedTeams
                            })
                        }


                        showNotification(data.message)
                    }}>
                        LOGIN
                </div>
                }</User.Consumer>
            </div>
        </div>
    )
}

export function RegisterModal() {

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [visible, setVisibility] = useState(false)
    changeRegisterModalVisibility = setVisibility

    if (!visible)
        return null

    return (
        <div onClick={() => { setRegisterModalVisibility(false) }} id="modal">
            <div className="login" id="dialog" onClick={(e) => { e.stopPropagation() }}>
                <p className="title">REGISTER</p>
                <p>Username:</p>
                <input className="username" onChange={(event) => { setUsername(event.target.value) }} type="text"></input>
                <p>Password:</p>
                <input className="password" onChange={(event) => { setPassword(event.target.value) }} type="password"></input>
                <div className="space"></div>
                <div className="btn default" onClick={async () => {
                    let data = await Register(username, password)

                    if (data.status === "success") {
                        setRegisterModalVisibility(false)
                    }

                    showNotification(data.message)
                }}>Register</div>
            </div>
        </div>
    )
}

export function CreatePostModal() { //Hide main page when shown, or?

    const [visible, setVisibility] = useState(false)
    const [title, setTitle] = useState('')
    const [body, setBody] = useState('')

    changeCreatePostModalVisibility = setVisibility

    if (!visible)
        return null

    return (
        <div id="create-post-dialog">
            <h2 className="title">Create a post</h2>
            <div className="divider"></div>
            <input placeholder="Enter title..." onChange={(e) => { setTitle(e.target.value) }}></input>
            <textarea placeholder="Enter description..." onChange={(e) => { setBody(e.target.value) }}></textarea>
            <div className="row">
                <Page.Consumer>
                    {page =>
                        <div className="button fixed" onClick={() => { submitPost(title, body, page.tags) }}>
                            <div className="items">
                                <p className="text">Post</p>
                            </div>
                            <div className="shadow"></div>
                        </div>
                    }
                </Page.Consumer>
                <div className="button fixed" onClick={() => { setVisibility(false) }}>
                    <div className="items">
                        <p className="text">Cancel</p>
                    </div>
                    <div className="shadow"></div>
                </div>
            </div>
        </div>
    )
}

async function submitPost(title, body, tags) {

    let data = await SubmitPost(title, body, tags, Date.now())

    if (data.status === "success")
        showNotification('Posted successfully')

    setCreatePostModalVisibility(false)
}

//optimize
export function setAcceptModalVisibility(visibility) {
    if (changeModalVisibility == null) {
        return
    }
    changeModalVisibility(visibility)
}

export function setLoginModalVisibility(visibility) {
    if (changeLoginModalVisibility == null)
        return

    changeLoginModalVisibility(visibility)
}

export function setRegisterModalVisibility(visibility) {
    if (changeRegisterModalVisibility == null)
        return

    changeRegisterModalVisibility(visibility)
}

export function setCreatePostModalVisibility(visibility) {
    if (changeCreatePostModalVisibility == null)
        return

    changeCreatePostModalVisibility(visibility)
}