import React, { useEffect, useState } from 'react'
import Post from './Post'
import { GetPosts } from '../Communicator'
import { setCreatePostModalVisibility } from './Modal'
import { isAuthenticated } from '../Utils'
import { Page } from '../Config'

function PostContainer(props) {

  const [posts, setPosts] = useState([])

  useEffect(() => {

    //This repeats 4 times

    async function fetchData() {
      let posts = await GetPosts(props.page.currentTags)
      setPosts(posts.data.posts)
    }

    fetchData()
  }, [props.page.currentTags])

  return (
    <div id="main-holder">
      <div id="post-container">
        <SearchBar></SearchBar>
        <FilterBar></FilterBar>
        <CreatePostButton></CreatePostButton>
        <Page.Consumer>
          {page =>
            posts.map(post => (
              <Post page={page} key={post._id} id={post._id} votes={post.votes} username={post.username} title={post.title} body={post.body} time={post.time}></Post>
            ))
          }
        </Page.Consumer>
      </div>
    </div>
  )
}

function SearchBar() {
  return (
    <div id="searchbar">
      <i className="fas fa-search"></i>
      <input placeholder="Search..."></input>
    </div>
  )
}

function FilterBar() {
  return (
    <div id="filterbar">
      <div id="filter-selector">
        <i className="fab fa-red-river"></i>
        <div className="divider"></div>
        <p>TRENDING</p>
      </div>

      <div id="view-selector">
        <p>VIEW</p>
        <div className="divider"></div>
        <i className="fas fa-th-list"></i>
        <i className="fas fa-list"></i>
        <i className="fas fa-stream"></i>
      </div>
    </div>
  )
}

function CreatePostButton() {

  if (!isAuthenticated())
    return null

  return (
    <div id="create-post-btn" onClick={() => { setCreatePostModalVisibility(true) }}>
      <div className="row">
        <i className="icon fas fa-envelope"></i>
        <p>CREATE POST</p>
      </div>
      <div className="shadow">
      </div>
    </div>
  )
}

export default PostContainer