import React, { useState, useEffect } from 'react'
import LeftMenu from './components/LeftMenu'
import PostContainer from './components/MainPage'
import RightMenu from './components/RightMenu'
import ContentPage from './components/ContentPage'
import { AcceptModal, LoginModal, RegisterModal, CreatePostModal } from './components/Modal'
import { NotificationBar } from './components/NoticationBar'
import { User, Page } from './Config'
import { isAuthenticated } from './Utils'
import { GetUserInfo } from './Communicator'
import { LoadScreen } from './components/LoadDialog'
import { RegisterPage } from './components/RegisterPage' //REMOVE
import './style.css'

function App(props) {

  const [menu, setMenu] = useState(0)

  useEffect(() => {

    async function getUser() {
      if (isAuthenticated()) {
        let result = await GetUserInfo()
        console.log(result.data)
        props.user.editUser({
          username: result.data.username,
          followingSports: result.data.followedSports,
          followingCompetitions: result.data.followedCompetitions,
          followingTeams: result.data.followedTeams
        })
      }
    }

    getUser()
  }, [])

  return (
    <>
      <User.Consumer>
        {user =>
          <>
            <RegisterPage></RegisterPage>
            <NavBar changeMenu={setMenu} menu={menu}></NavBar>
            <LoadScreen></LoadScreen>
            <AcceptModal></AcceptModal>
            <LoginModal></LoginModal>
            <CreatePostModal></CreatePostModal>
            <RegisterModal></RegisterModal>
            <Page.Consumer>
              {page =>
                <PostContainer page={page}></PostContainer>
              }
            </Page.Consumer>
            <NotificationBar></NotificationBar>
            <LeftMenu activated={menu === 1 ? true : false}></LeftMenu>
            <RightMenu activated={menu === 2 ? true : false}></RightMenu>
          </>
        }
      </User.Consumer>
    </>
  )
}

function NavBar(props) {

  const onMenuClick = (val) => {

    if (props.menu === val) {
      props.changeMenu(0)
      return
    }

    props.changeMenu(val)
  }

  return (
    <div id="navbar">
      <i className="fas fa-bars" onClick={() => { onMenuClick(1) }}></i>
      <img alt="logo" src="./media/logo.png" />
      <i className="fas fa-bars" onClick={() => { onMenuClick(2) }}></i>
    </div>)
}

export default App
