import React, { useState, useEffect } from 'react'
import { DoVote } from '../Communicator'

export default function Post(props) {

  let timeVal = parseInt(props.time)
  let date = new Date(timeVal)

  const [votes, setVotes] = useState(0)

  useEffect(() => {

    let totalVotes = 0

    for(let i = props.votes.length - 1; i >= 0; i--){
      totalVotes += props.votes[i].votes

      if(props.votes[i].tag === props.page.currentTags)
        break
    }

    setVotes(totalVotes)
  }, [props.page.currentTags])

  return (
        <div className="post">
          <div className="header">
            <h1>{props.title}</h1>
          </div>
          <p className="description">{props.body}</p>
          <div className="divider"></div>
          <div className="row">
            <div className="column">
              <div className="user-details">
                <p className="username">{props.username}</p>
                <div className="circle"></div>
                <i className="emblem fas fa-fire"></i>
              </div>
              <div className="timestamp">
                <p>{date.toLocaleString()}</p>
              </div>
            </div>
            <div className="voter">
              <div className="column">
                <i onClick={() => { likePost(props.id, props.page.currentTags,() => { setVotes(votes + 1) }) }} className="upvote fas fa-caret-up"></i>
                <p className="votes">{votes}</p>
                <i onClick={() => { dislikePost(props.id, props.page.currentTags, () => { setVotes(votes - 1) }) }} className="downvote fas fa-caret-down"></i>
              </div>
            </div>
          </div>
        </div>
  )
}

function likePost(id, tag, callback) {
  DoVote('add', tag, id)
    .then((resp) => {
      if (resp.status === "success")
        callback()
    })
}

function dislikePost(id, tag, callback) {
  DoVote('subtract', tag, id)
    .then((resp) => {
      if (resp.status === "success")
        callback()
    })
}